$all-typeface-tokens: map-merge(
  $system-typeface-tokens,
  $theme-typeface-tokens
);

/*
----------------------------------------
Typescale
----------------------------------------
*/

$system-type-scale: (
  "micro": 10px,
  1: 12px,
  2: 13px,
  3: 14px,
  4: 15px,
  5: 16px,
  6: 17px,
  7: 18px,
  8: 20px,
  9: 22px,
  10: 24px,
  11: 28px,
  12: 32px,
  13: 36px,
  14: 40px,
  15: 48px,
  16: 56px,
  17: 64px,
  18: 80px,
  19: 120px,
  20: 140px,
);

/*
----------------------------------------
Measure
----------------------------------------
*/

$system-measure-smaller: 44ex;
$system-measure-small: 60ex;
$system-measure-base: 64ex;
$system-measure-large: 68ex;
$system-measure-larger: 72ex;
$system-measure-largest: 88ex;

/*
----------------------------------------
Line height
----------------------------------------
*/

$system-line-height: (
  1: 1,
  2: 1.2,
  3: 1.35,
  4: 1.5,
  5: 1.62,
  6: 1.75,
);

/*
----------------------------------------
Base cap height
----------------------------------------
500px 'N' height measured in Sketch.
Used for normalizing font sizes.
Current normalized to Apple system
fonts.
----------------------------------------
*/

$system-base-cap-height: 362px;

/*
----------------------------------------
Luminance ranges
----------------------------------------
*/

$system-color-grades: (
  100: (
    "min": 0,
    "max": 0,
  ),
  90: (
    "min": 0.005,
    "max": 0.015,
  ),
  80: (
    "min": 0.02,
    "max": 0.04,
  ),
  70: (
    "min": 0.05,
    "max": 0.07,
  ),
  60: (
    "min": 0.1,
    "max": 0.125,
  ),
  50: (
    "min": 0.175,
    "max": 0.183,
  ),
  40: (
    "min": 0.25,
    "max": 0.3,
  ),
  30: (
    "min": 0.35,
    "max": 0.45,
  ),
  20: (
    "min": 0.5,
    "max": 0.65,
  ),
  10: (
    "min": 0.75,
    "max": 0.82,
  ),
  5: (
    "min": 0.85,
    "max": 0.93,
  ),
  0: (
    "min": 1,
    "max": 1,
  ),
);

$theme-color-grades: (
  "lightest": (
    "min": 5,
    "max": 10,
  ),
  "lighter": (
    "min": 10,
    "max": 20,
  ),
  "light": (
    "min": 30,
    "max": 40,
  ),
  "root": (
    "min": 50,
    "max": 50,
    "vivid": (
      "min": 30,
      "max": 50,
    ),
  ),
  "dark": (
    "min": 60,
    "max": 60,
  ),
  "darker": (
    "min": 70,
    "max": 80,
  ),
  "darkest": (
    "min": 90,
    "max": 90,
  ),
);

